<template>
    <div class="day" @click="activate" :class="{
        active: isActiv,
    }">
        <p>{{day.getDate()}}</p>
    </div>
</template>

<script>
import { is } from '@babel/types';

    export default {
        name:'cal-day',
        data(){
            return{
                isActiv: false,
            }
        },
        props:{
            day:{
                type: Date
            }
        },
        methods:{
            activate(){this.isActiv = !this.isActiv}
        }
    }
</script>

<style scoped>
.day{
    height: 30px;
    width: 30px;
    /* background: rgb(167, 207, 211); */
    box-shadow: 0px 0px 4px 0px rgba(244, 187, 144, 0.2);
    border-bottom: 1px solid #fff;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2px;
    cursor:pointer;
}
.day:hover{
    background: rgba(244, 187, 144, 0.2); 
}
.active{
    background: rgb(87, 139, 181);
    color:rgb(41, 40, 38);  
}
</style>