<template>
    <div class="modal-wrap" @mousedown="hideModal">
        <div class="modal" @mousedown.stop>
            <div class="modal-title"><h3>Результат</h3></div>
            <div v-if="result.length>0" class="modal-item" v-for="item in result">
                <p>{{  item  }}</p>
            </div>
            <div v-else class="loader">Загрузка...</div>
            
        </div>

    </div>
</template>

<script>
    export default {
        name:'modal-result',
        data(){
            return{
                num:0
            }
        },
        props:{
            result: {type: Array}
        },
        methods:{
            hideModal(){
                this.$emit('hideModal')
            },
            score(){
                this.num+=1;
            }
        }

    }
</script>

<style scoped>
.modal-wrap{
    position: fixed;
    
    height: 100%;
    width: 100%;
    background: rgba(0,0,0,0.5);
    z-index: 1;
}
.modal{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);

    height: 500px;
    width: 400px;
    background: #fff;
    border: 1px solid rgb(140, 140, 140);
    box-shadow: 0px 0px 15px 5px rgba(39, 44, 47, 0.6);

    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.modal-item{
    font-size: 16px;
    margin: 4px;
    width: 250px;
    display: flex;
    justify-content: space-between ;


}
.modal-title{
margin: 16px;
border-bottom: 1px solid rgb(140, 140, 140);
}

</style>